import * as React from 'react';
import { DD_RIGHT, DD_LEFT } from './styles';

type PropType = {
  option: {
    OPTION_NAME: string;
    'SUM(COVERAGE)': number;
  }[];
};

export function OptionBoard(props: PropType) {
  console.log(props.option);
  console.log(typeof props.option);
  let implant = 0;
  let teulni = 0;
  let bridge = 0;

  let legin = 0;
  let crown = 0;
  let amalgam = 0;

  let singyeong = 0;

  let itmom = 0;
  let chizogol = 0;
  let scaling = 0;
  let goljeol = 0;
  let x_ray = 0;

  for (const item of props.option) {
    if (item['OPTION_NAME'] == '') {
      legin = item['SUM(COVERAGE)'];
    } else if (item['OPTION_NAME'] == '') {
      crown = item['SUM(COVERAGE)'];
    } else if (item['OPTION_NAME'] == '') {
      amalgam = item['SUM(COVERAGE)'];
    } else if (item['OPTION_NAME'] == '') {
      implant = item['SUM(COVERAGE)'];
    } else if (item['OPTION_NAME'] == '') {
      teulni = item['SUM(COVERAGE)'];
    } else if (item['OPTION_NAME'] == '') {
      bridge = item['SUM(COVERAGE)'];
    } else if (item['OPTION_NAME'] == '') {
      singyeong = item['SUM(COVERAGE)'];
    } else if (item['OPTION_NAME'] == '') {
      itmom = item['SUM(COVERAGE)'];
    } else if (item['OPTION_NAME'] == ' ') {
      chizogol = item['SUM(COVERAGE)'];
    } else if (item['OPTION_NAME'] == '') {
      scaling = item['SUM(COVERAGE)'];
    } else if (item['OPTION_NAME'] == ' ') {
      goljeol = item['SUM(COVERAGE)'];
    } else if (item['OPTION_NAME'] == 'X-RAY ') {
      x_ray = item['SUM(COVERAGE)'];
    }
  }

  return (
    <div>
      <dl>
        <dt></dt>
        <dd>
          <DD_LEFT></DD_LEFT>
          <DD_RIGHT>{implant ? implant : '0'}</DD_RIGHT>
        </dd>
        <dd>
          <DD_LEFT></DD_LEFT>
          <DD_RIGHT>{teulni ? teulni : '0'}</DD_RIGHT>
        </dd>
        <dd>
          <DD_LEFT></DD_LEFT>
          <DD_RIGHT>{bridge ? bridge : '0'}</DD_RIGHT>
        </dd>
      </dl>
      <dl>
        <dt></dt>
        <dd>
          <DD_LEFT></DD_LEFT>
          <DD_RIGHT>{crown ? crown : '0'}</DD_RIGHT>
        </dd>
        <dd>
          <DD_LEFT></DD_LEFT>
          <DD_RIGHT>{amalgam ? amalgam : '0'}</DD_RIGHT>
        </dd>
        <dd>
          <DD_LEFT></DD_LEFT>
          <DD_RIGHT>{legin ? legin : '0'}</DD_RIGHT>
        </dd>
      </dl>
      <dl>
        <dt></dt>
        <dd>
          <DD_LEFT></DD_LEFT>
          <DD_RIGHT>{singyeong ? singyeong : '0'}</DD_RIGHT>
        </dd>
      </dl>
      <dl>
        <dt></dt>
        <dd>
          <DD_LEFT></DD_LEFT>
          <DD_RIGHT>{itmom ? itmom : '0'}</DD_RIGHT>
        </dd>
        <dd>
          <DD_LEFT> </DD_LEFT>
          <DD_RIGHT>{chizogol ? chizogol : '0'}</DD_RIGHT>
        </dd>
        <dd>
          <DD_LEFT></DD_LEFT>
          <DD_RIGHT>{scaling ? scaling : '0'}</DD_RIGHT>
        </dd>
        <dd>
          <DD_LEFT> </DD_LEFT>
          <DD_RIGHT>{goljeol ? goljeol : '0'}</DD_RIGHT>
        </dd>
        <dd>
          <DD_LEFT>X-RAY </DD_LEFT>
          <DD_RIGHT>{x_ray ? x_ray : '0'}</DD_RIGHT>
        </dd>
      </dl>
    </div>
  );
}
